import React, { useState, useEffect } from "react";
// import axios from "axios";
import "./css/common.css";
import Header from "./components/header/header.js";
import Modal from "./components/modal.js";
import KakaoMap from "./components/kakaoMap.js";
import eduCenterData from "./data/eduCenterData.js";

function App() {
  const [modal, setModal] = useState(false);
  const [centerInfo, setCenterInfo] = useState(eduCenterData);
  const [getMap, setGetMap] = useState({});
  const [centerMapTitle, setCenterMapTitle] = useState(" ");
  const [centerMapOffice, setCenterMapOffice] = useState("KR");
  // const [loading, setLoading] = useState(false);

  return (
    <div className="App">
      <Header
        centerMapTitle={centerMapTitle}
        centerMapOffice={centerMapOffice}
      />
      {/* {loading ? <div className="loading">...</div> : null} */}
      <KakaoMap
        setGetMap={setGetMap}
        centerInfo={centerInfo}
        setModal={setModal}
      />
      {modal === true ? (
        <Modal getMap={getMap} centerInfo={centerInfo} setModal={setModal} />
      ) : null}
    </div>
  );
}

export default App;
